<template>
<v-ons-page>
<v-ons-card>
  <div>

    <h1 class="neon">WINNER:{{ranking[0].name}}</h1>

    <ul v-for ="(item,index) of ranking" v-bind:key="index" class="ranking">
      <ol>{{index+1}}位:{{item.name}}</ol>
    </ul>
  </div>
  </v-ons-card>
  <div>
   <ons-button type="submit" @click="submit()">
    もう一度
  </ons-button>
  </div>
  </v-ons-page>
</template>

<script>
import page1 from './page1.vue'
import page2 from './page2.vue'

export default {
  data() {
    return {
      ranking: this.$store.state.ranking,
    };
  },
  created() {
  },
  methods: {
    submit: function(){
    this.$store.commit("navigator/push", page1);
  }
  }
};
</script>

<style scoped>
h1{
  text-align: center;
}

body{
  margin: 0;
  padding: 0;
  background: #05151a;
}

.neon{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin: 0;
  padding: 0 20px;
  font-size: 3em;
  color: #ffd700;
  text-shadow: 0 0 50px #ffd700;
  letter-spacing: 5px;
}

.neon:after{
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 20px;
  z-index: -1;
  color: #ffd700;
  filter: blur(15px);
}

.neon:before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffd700;
  z-index: -2;
  opacity: .3;
  filter: blur(50px);
}
.ranking{
text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ranking ol{
    position: relative;
    margin: 0;
    padding: 0 0 0 16px;
    border-bottom: 1px dotted #ccc;
}
.ranking ol::before{
    position: absolute;
    top: 7px;
    left: 1px;
    color: #d05;
    content: "★";
    font-size: 9px;
}
.ranking ol:first-child::before,
.ranking ol:nth-child(2)::before,
.ranking ol:nth-child(3)::before{
    position: absolute;
    top: 1px;
    left: 0;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-bottom: 10px solid #fb0;
    content: "";
}
.ranking ol:first-child::after,
.ranking ol:nth-child(2)::after,
.ranking ol:nth-child(3)::after{
    position: absolute;
    top: 9px;
    left: 0;
    width: 0;
    height: 0;
    border: 6px solid #fb0;
    border-top: 3px solid transparent;
    border-bottom: 6px solid #f90;
    content: "";
}
.ranking ol:nth-child(2)::before{
    border-bottom: 10px solid #ccc;
}
.ranking ol:nth-child(2)::after{
    border: 6px solid #ccc;
    border-top: 3px solid transparent;
    border-bottom: 6px solid #aaa;
}
.ranking ol:nth-child(3)::before{
    border-bottom: 10px solid #d98;
}
.ranking ol:nth-child(3)::after{
    border: 6px solid #d98;
    border-top: 3px solid transparent;
    border-bottom: 6px solid #c76;
}
.ranking ol span{
    color: #9ab;
    font-size: 11px;
}
/*
.box17{
    margin:2em 0;
    position: relative;
    padding: 0.5em 1.5em;
    border-top: solid 2px black;
    border-bottom: solid 2px black;
}
.box17:before, .box17:after{
    content: '';
    position: absolute;
    top: -10px;
    width: 2px;
    height: -webkit-calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: black;
}
.box17:before {left: 10px;}
.box17:after {right: 10px;}
.box17 p {
    margin: 0;
    padding: 0;
}
*/
.wrapper {
  margin: 150px auto;
  text-align: center;
  width: 100%;
  position: relative;
}
.btn{
  padding: 15px 100px;
  margin:10px 4px;
  color: #fff;
  font-family: sans-serif;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  text-decoration: none;
  /* display:inline-block; */
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
}
.btn::before {
  content: '';
  position: absolute;
  bottom: 0%;
  left: 0px;
  width: 100%;
  height: 1px;
  background: #6098FF;
  display: block;
  -webkit-transform-origin: right top;
  -ms-transform-origin: right top;
  transform-origin: right top;
  -webkit-transform: scale(0, 1);
  -ms-transform: scale(0, 1);
  transform: scale(0, 1);
  -webkit-transition: transform 0.4s cubic-bezier(1, 0, 0, 1);
  transition: transform 0.4s cubic-bezier(1, 0, 0, 1)
}

.btn:hover::before {
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1)
}

</style>




