<template>
  <v-ons-page>
    <h1 style="color: #ffffff;" align="center">Smart Poker</h1>
    <ons-card>
      <div class="flex_box">
      <div class="text_content">
        <div>
          <h1>
            ラウンド{{round}}
            <br />
          </h1>
              <h2>
            {{bettingRound[br]}}</h2>
            <br />
            <h1>
            {{winComment}}
          </h1>
          <div>
            <h3>ディーラーは{{dealer}}です。</h3>
          </div>
          <div>
            <h3>{{turn}}の番です。</h3>
          </div>
          <br />
          <div>
            <h1>ポット:{{pot}}</h1>
          </div>
          <div>
            <h2>ベット:{{pay}}</h2>
          </div>
          <div>
            <h2>{{comment}}</h2>
          </div>
        </div>
      </div>


      <div style="padding-right:60px">
        <br />
        <br />
        <div class="table_content">
          <table border="2">
            <tr>
              <th>Player</th>
              <th>Turn</th>
              <th>Name</th>
              <th>Chip</th>
              <th>Bet</th>
            </tr>
            <tr
              v-for="(item,i) of players"
              v-bind:key="item.name"
              v-bind:class="{active: item.name==turn}"
            >
              <td>PLAYER{{i+1}}</td>
              <td align="center">
                <div v-if="turn==item.name">★</div>
                <div v-if="item.isplay==1">Fold</div>
              </td>
              <td>{{ item.name }}</td>
              <td>{{ item.chip }}</td>
              <td>{{ item.bet }}</td>


            </tr>
          </table>
        </div>
      </div>
      </div>
      <br />
      <modal name="winner-modal">
        <div align="center">
          <h2>ショーダウン</h2>
          ラウンド{{round}}終了です。
          勝利プレイヤーを「プレイヤー'番号'」で言ってください。
        </div>
      </modal>
    </ons-card>
  </v-ons-page>
</template>

<script>
import page1 from "./page1.vue";
import page3 from "./page3.vue";

export default {
  data() {
    return {
      player: this.$store.state.player,
      maxRound: this.$store.state.maxRound,
      chip: this.$store.state.chip,
      minBet: this.$store.state.minBet,
      array: [],
      round: 1,
      dealer: "",
      pot: 0,
      bettingRound: ["プリフロップ", "フロップ", "ターン", "リバー"],
      br: 0,
      players: [],
      allBet: 0,
      order: "",
      turn: "",
      action: "",
      p: 0,
      selectActionValue: 2,
      selectActionOptions: {
        1: "ベット",
        2: "コール",
        3: "レイズ",
        4: "チェック",
        5: "フォールド",
        6: "オールイン"
      },
      selectWinnerValue: "",
      update: null,
      pay: 10,
      playing: 0,
      comment: "",
      winComment: "",
      checkCount: 0,
      record: [],
      timer: null,
      w: 0,
      win: 9,
      wc: 0
    };
  },
  watch: {
    update: function() {
      this.w++;
      if (this.w != 1 && this.wc == 0) {
        console.log("update");
        this.submit();
      } else if (this.wc == 1) {
        console.log("winner");
        this.winner();
      }
    },
    win: function() {
      if (this.win != "" || this.win == 0) {
        console.log("winner");
        this.winner();
      }
    }
  },
  created() {
    //プレイヤー設定
    for (let i = 0; i < this.player.length; i++) {
      let addPlayer = {
        name: this.player[i],
        chip: this.chip,
        bet: 0,
        isplay: 0
      };
      this.players.push(addPlayer);
    }
    //席順
    for (let i = this.players.length - 1; i >= 0; i--) {
      let rand = Math.floor(Math.random() * (i + 1));
      [this.players[i], this.players[rand]] = [
        this.players[rand],
        this.players[i]
      ];
    }
    //席順のコンソール出力
    let q = 0;
    for (let item in this.players) {
      this.order = this.order + this.players[item]["name"] + ",";
      q++;
      if (q == this.players.length) {
        console.log(this.order + "の順です。");
      }
    }
    console.log("ラウンド:" + this.round);
    //プリフロップ
    console.log(this.bettingRound[0]);
    //ディーラーを決定
    this.dealer = this.players[0].name;
    console.log("ディーラー:" + this.dealer);
    this.playing = this.players.length;
    this.sbbb();
  },
  computed: {},
  methods: {
    submit: function() {
      console.log("submit");
      this.winComment = "";
      //this.action = this.selectActionOptions[this.selectActionValue];
      if (this.pay <= this.players[this.p % this.players.length].chip) {
        if (this.pay >= this.minBet) {
          //アクション
          switch (this.action) {
            case "ベット":
              this.betting();
              break;
            case "コール":
              this.call();
              break;
            case "レイズ":
              this.raise();
              break;
            case "チェック":
              this.check();
              break;
            case "フォールド":
              this.fold();
              break;
            case "オールイン":
              this.allIn();
              break;
          }
        } else if (this.action == "チェック") {
          switch (this.action) {
            case "チェック":
              this.check();
              break;
          }
        } else if (this.action == "フォールド") {
          switch (this.action) {
            case "フォールド":
              this.fold();
              break;
          }
        } else if (this.action == "コール") {
          switch (this.action) {
            case "コール":
              this.call();
              break;
          }
        } else {
          this.comment = "最低ベット額は" + this.minBet + "です";
        }
      } else if (this.action == "フォールド") {
        switch (this.action) {
          case "フォールド":
            this.fold();
            break;
        }
      } else if (this.action == "チェック") {
        switch (this.action) {
          case "チェック":
            this.check();
            break;
        }
      } else {
        this.comment = "所持しているチップ数を超えています";
      }
    },
    betting() {
      console.log("ベット");
      if (
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0
      ) {
        this.stopTimer();
        this.checkCount = 0;
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip -
          this.players[this.p % this.players.length].bet;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "既にベットされています";
      }
    },
    call() {
      console.log("コール");
      if (
        this.players[this.p % this.players.length].bet <
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        this.stopTimer();
        this.checkCount = 0;
        if (
          Math.max.apply(
            null,
            this.players.map(function(o) {
              return o.isplay;
            })
          ) == 2
        ) {
          this.players[this.p % this.players.length].isplay = 2;
          this.playing--;
        }
        this.pay = Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        );
        let pay2 =
          Number(this.pay) - this.players[this.p % this.players.length].bet;
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip - pay2;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else if (
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0
      ) {
        this.stopTimer();
        this.checkCount = 0;
        if (
          Math.max.apply(
            null,
            this.players.map(function(o) {
              return o.isplay;
            })
          ) == 2
        ) {
          this.players[this.p % this.players.length].isplay = 2;
          this.playing--;
        }
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip -
          this.players[this.p % this.players.length].bet;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "コールできません";
      }
    },
    raise() {
      console.log("レイズ");
      if (
        this.pay >=
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        if (
          Number(this.pay) +
            Number(
              Math.max.apply(
                null,
                this.players.map(function(o) {
                  return o.bet;
                })
              )
            ) <=
          this.players[this.p % this.players.length].chip
        ) {
          this.stopTimer();
          this.checkCount = 0;
          this.pay =
            Number(this.pay) +
            Math.max.apply(
              null,
              this.players.map(function(o) {
                return o.bet;
              })
            );
          let pay2 =
            Number(this.pay) - this.players[this.p % this.players.length].bet;
          this.players[this.p % this.players.length].bet = this.pay;
          this.players[this.p % this.players.length].chip =
            this.players[this.p % this.players.length].chip - pay2;
          console.log(
            "ベット額:" + this.players[this.p % this.players.length].bet
          );
          console.log(
            "チップ:" + this.players[this.p % this.players.length].chip
          );
          this.nextTurn();
        } else {
          this.comment = "チップが足りません";
        }
      } else {
        this.comment = "ベットの2倍以上でレイズしてください";
      }
    },
    check() {
      console.log("チェック");
      let fp = this.round + 1;
      while (this.players[fp % this.players.length].isplay != 0) {
        fp++;
      }
      if (
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0
      ) {
        this.stopTimer();
        this.checkCount++;
        if (this.checkCount == this.playing) {
          this.checkCount = 0;
          this.nextBr();
        } else {
          this.nextTurn();
        }
        //プリフロップbb round+1=bb
      } else if (
        this.br == 0 &&
        this.p % this.players.length == fp % this.players.length
      ) {
        this.nextBr();
      } else {
        this.comment = "既にベットされています";
      }
    },
    fold() {
      console.log("フォールド");
      this.stopTimer();
      this.players[this.p % this.players.length].isplay = 1;
      console.log(
        this.players[this.p % this.players.length].name + ":フォールド"
      );
      this.playing--;
      this.nextTurn();
    },
    allIn() {
      console.log("オールイン");
      if (
        this.players[this.p % this.players.length].chip +
          this.players[this.p % this.players.length].bet >=
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        this.stopTimer();
        this.checkCount = 0;
        this.players[this.p % this.players.length].bet =
          this.players[this.p % this.players.length].bet +
          this.players[this.p % this.players.length].chip;
        this.players[this.p % this.players.length].chip = 0;
        this.players[this.p % this.players.length].isplay = 2;
        this.playing--;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "チップが足りません";
      }
    },
    winner() {
      if (this.win != "" || this.win == 0) {
        this.stopTimer();
        this.players[this.win].chip = this.players[this.win].chip + this.pot;
        this.winComment = this.players[this.win].name + "さんが勝ちました";
        this.$modal.hide("winner-modal");
        this.wc = 0;
        this.nextRound();
      }
    },
    nextTurn() {
      this.comment = "";
      //isplayer==0のベットが全て同じか
      let eBet = 0;
      for (let i = 0; i < this.players.length; i++) {
        if (this.players[this.p % this.players.length].isplay == 0) {
          if (
            this.players[this.p % this.players.length].bet ==
            this.players[i].bet
          ) {
            if (this.players[i].bet != 0) {
              eBet++;
            }
          }
        }
      }
      console.log(eBet + "." + this.playing + "." + this.players.length);
      //nextTurn
      if (
        eBet != this.playing ||
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0 ||
        (this.br == 0 &&
          this.p <
            Number(
              this.players.length + (this.round % this.players.length) + 1
            ))
      ) {
        console.log("eBet != this.playing ||");
        this.p++;
        //フォールドした人を飛ばす
        while (this.players[this.p % this.players.length].isplay != 0) {
          this.p++;
          if (this.p == this.maxRound + this.players.length) {
            break;
          }
        }
        //全員フォールドしたとき
        if (this.playing == 1) {
          //全員フォールドしたとき
          if (
            Math.max.apply(
              null,
              this.players.map(function(o) {
                return o.isplay;
              })
            ) == 1
          ) {
            for (let i = 0; i < this.players.length; i++) {
              this.allBet = Number(this.allBet) + Number(this.players[i].bet);
              this.players[i].bet = 0;
            }
            this.pot = this.pot + Number(this.allBet);
            this.allBet = 0;
            this.players[this.p % this.players.length].chip =
              this.players[this.p % this.players.length].chip + this.pot;
            this.winComment =
              this.players[this.p % this.players.length].name +
              "さんが勝ちました";
            this.nextRound();
          } else {
            this.stopTimer();
            this.startTimer(); //timer
            this.turn = this.players[this.p % this.players.length].name;
            console.log(this.turn + "の番");
          }
          //オールイン
        } else if (this.playing == 0) {
          console.log("allincall");
          let ow = 0;
          for (let i = 0; i < this.players.length; i++) {
            this.allBet = Number(this.allBet) + Number(this.players[i].bet);
            this.players[i].bet = 0;
            if (this.players[i].isplay == 2) {
              ow++;
            }
          }
          //オールイン一人
          if (ow == 1) {
            for (let i = 0; i < this.players.length; i++) {
              if (this.players[i].isplay == 2) {
                this.pot = this.pot + Number(this.allBet);
                this.allBet = 0;
                this.players[i].chip = this.players[i].chip + this.pot;
                this.winComment = this.players[i].name + "さんが勝ちました";
                this.nextRound();
              }
            }
          }
          this.pot = this.pot + Number(this.allBet);
          this.allBet = 0;
          this.show();
        } else {
          this.stopTimer();
          this.startTimer(); //timer
          this.turn = this.players[this.p % this.players.length].name;
          console.log(this.turn + "の番");
        }
      } else {
        console.log("else");
        if (this.playing == 0) {
          let ow = 0;
          for (let i = 0; i < this.players.length; i++) {
            this.allBet = Number(this.allBet) + Number(this.players[i].bet);
            this.players[i].bet = 0;
            if (this.players[i].isplay == 2) {
              ow++;
            }
          }
          //オールイン一人
          if (ow == 1) {
            this.pot = this.pot + Number(this.allBet);
            this.allBet = 0;
            for (let i = 0; i < this.players.length; i++) {
              if (this.players[i].isplay == 2) {
                console.log("オールイン" + this.players[i].name);
                this.players[i].chip = this.players[i].chip + this.pot;
                this.winComment = this.players[i].name + "さんが勝ちました";
                this.nextRound();
              }
            }
          } else {
            this.pot = this.pot + Number(this.allBet);
            this.allBet = 0;
            this.show();
          }
        } else {
          this.nextBr();
        }
      }
    },
    nextBr() {
      //ベットをポットに移動
      for (let i = 0; i < this.players.length; i++) {
        this.allBet = Number(this.allBet) + Number(this.players[i].bet);
        this.players[i].bet = 0;
      }
      this.pot = this.pot + Number(this.allBet);
      this.allBet = 0;
      if (this.br < 3) {
        //ベッティングラウンドを進める
        this.br = this.br + 1;
        console.log(this.bettingRound[this.br]);
        this.p = this.round % this.players.length;
        while (this.players[this.p % this.players.length].isplay != 0) {
          this.p++;
          if (this.p == this.maxRound + this.players.length) {
            break;
          }
        }
        this.stopTimer();
        this.startTimer(); //timer
        this.turn = this.players[this.p % this.players.length].name;
        console.log(this.turn + "の番");
      } else {
        if (this.action != "フォールド") {
          this.show();
        }
      }
    },
    nextRound() {
      //次のラウンド・ディーラーを回す
      if (this.round < this.maxRound) {
        this.round += 1;
        this.pot = 0;
        this.br = 0;
        this.playing = this.players.length;
        for (let i = 0; i < this.players.length; i++) {
          if (this.players[i].chip > 0) {
            this.players[i].isplay = 0;
          } else {
            this.players[i].isplay = 1;
            this.playing--;
          }
          if (this.playing == 1) {
            this.sort();
            this.stopTimer();
            this.$store.commit("navigator/push", page3);
          }
        }
        this.p = (this.round - 1) % this.players.length;
        while (this.players[this.p % this.players.length].isplay == 1) {
          this.p++;
        }
        this.dealer = this.players[this.p % this.players.length].name;
        //コンソール出力
        console.log("ディーラー:" + this.dealer);
        console.log(this.bettingRound[0]);
        //SB.BBのベット
        this.sbbb();
      } else {
        this.sort();
        //結果のページに遷移
        this.stopTimer();
        this.$store.commit("navigator/push", page3);
      }
    },
    sbbb() {
      //sb
      this.p = this.round % this.players.length;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.players[this.p % this.players.length].bet = this.minBet / 2;
      this.players[this.p % this.players.length].chip =
        this.players[this.p % this.players.length].chip -
        this.players[this.p % this.players.length].bet;
      console.log(this.players[this.p % this.players.length].name + "の番");
      console.log("ベット額:" + this.players[this.p % this.players.length].bet);
      console.log("チップ:" + this.players[this.p % this.players.length].chip);
      //bb
      this.p++;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.players[this.p % this.players.length].bet = this.minBet;
      this.players[this.p % this.players.length].chip =
        this.players[this.p % this.players.length].chip -
        this.players[this.p % this.players.length].bet;
      console.log(this.players[this.p % this.players.length].name + "の番");
      console.log("ベット額:" + this.players[this.p % this.players.length].bet);
      console.log("チップ:" + this.players[this.p % this.players.length].chip);
      //utg
      this.p++;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.turn = this.players[this.p % this.players.length].name;
      console.log(this.players[this.p % this.players.length].name + "の番");
      this.stopTimer();
      this.startTimer();//timer
    },
    show() {
      this.wc ++;
      this.startTimerWinner();
      this.$modal.show("winner-modal");
    },
    sort() {
      console.log(this.players);
      this.players.sort(function(a, b) {
        if (a.chip > b.chip) return -1;
        if (a.chip < b.chip) return 1;
        return 0;
      });
      this.$store.state.ranking = this.players;
    },
    fetch() {
      let that = this;
      console.log("fetch" + this.w);
      fetch(
        this.$url + "/record" + "?recordID=" + localStorage.getItem("recordID"),
        { method: "GET" }
      )
        .then(res => {
          // 返されたBodyをjsonにしてPromise.resolve()する
          return res.json();
        })
        .then(data => {
          // api_url から返されたデータ(json)
          // 実際にデータを加工したり色々する
          console.log(data);
          that.action = data.record.action.value;
          if (that.action == "レイズ" || that.action == "ベット") {
            that.pay = data.record.point.value;
          }
          that.update = data.record.time.value;
        })
        .catch(err => {
          console.error(err);
        });
    },
    startTimer() {
      let that = this;
      console.log("interval");
      that.timer = setInterval(function() {
        that.fetch();
      }, 3000);
    },
    stopTimer() {
      let that = this;
      clearInterval(this.timer);
      console.log("stopped");
    },
    fetchWinner() {
      let that = this;
      console.log("fetchWinner");
      fetch(
        this.$url + "/record" + "?recordID=" + localStorage.getItem("recordID"),
        { method: "GET" }
      )
        .then(res => {
          // 返されたBodyをjsonにしてPromise.resolve()する
          return res.json();
        })
        .then(data => {
          // api_url から返されたデータ(json)
          // 実際にデータを加工したり色々する
          console.log(data);
          if (data.record.winner.value != "") {
            this.win = data.record.winner.value - 1;
            console.log(this.win);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    startTimerWinner() {
      let that = this;
      console.log(this.wc);
      that.timer = setInterval(function() {
        that.fetchWinner();
      }, 3000);
    }
  }
};
</script>


<style scoped>

.flex_box{
  display: flex;

}

.text_content{
  /* margin-left:10px; */
 padding-left:5%;
}
.table_content{
  /* margin-right: 10px; */
  padding-right:20%;
  width: 200%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: auto;
}

table tr {
  border-bottom: solid 1px #eee;
  cursor: pointer;
}

table tr:hover {
  background-color: #d4f0fd;
}

table th,
table td {
  text-align: center;
  width: 25%;
  padding: 15px 0;
}
</style>



